h1 {
  font-size: 40px;
  margin: 0;
}

#Browser {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

#browserSearch {
  background-color: gray;
  padding: 2.5% 5%;
  width: fit-content;
  height: fit-content;
  overflow: auto;
  position: relative;
  top: 0%;
  bottom: 0%;
  animation-name: browserMove;
  animation-duration: 1s;
  animation-timing-function: ease-out;
}

@keyframes browserMove {
  from {
    top: -65%;
    bottom: 100%;
  }
  to {
    top: 0%;
    bottom: 0%;
  }
}

/*
#browserSearch > * {
  padding: 1% 0%;
}
*/

.hero {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
}

.browserMove-enter {
  opacity: 0.01;
}

.browserMove-enter.browserMove-enter-active {
  opacity: 1;
  transition: opacity 300ms ease-in;
}

.browserMove-leave {
  top: 50%;
}

.browserMove-leave.browserMove-leave-active {
  top: 20%;
  transition: top 300ms ease-in-out;
}

