.favoritesBar > div > div {
  display: grid;
  grid-template-columns: 1fr 77px;
  text-align: left;
  padding: 2%;
}

.favoritesBar > div {
  overflow-y: auto;
  overflow-x: hidden;
  height: calc(80vh - 0.83em - 0.24em - 36px);
  width: 95%;
}

.favoritesBar > h2 {
  text-align: center;
}

.favoritesBar {
  margin: 2vw 0 1% 0vw;

  padding: 1%;
  width: 19%;
  height: 80vh;
  display: inline-block;
  position: relative;
  animation-name: openFavorites;
  animation-duration: 0.7s;
  animation-timing-function: ease-out;
}

@keyframes openFavorites {
  from {
    width: 2%;
  }
  to {
    width: 19%;
  }
}

.favoritesBarClosed {
  width: 2%;
  animation-name: closeFavorites;
  animation-duration: 0.7s;
  animation-timing-function: ease-out;
  overflow: hidden;
}

.favoritesBarClosed > div > div {
  display: none;
  overflow: hidden;
}

.favoritesBarClosed > div {
  overflow-y: hidden;
  overflow-x: hidden;
}

.favoritesBarClosed > h2 {
  display: none;
}

.favoritesBarClosed > .favorite {
  display: none;
}

@keyframes closeFavorites {
  from {
    width: 19%;
  }
  to {
    width: 2%;
  }
}

.favoritesBar > .favoriteToggle {
  width: 1.5vw;
  animation-name: resizeFavoritesTogglerSmall;
  animation-duration: 0.7s;
  animation-timing-function: ease-out;
}

@keyframes resizeFavoritesTogglerSmall {
  from {
    width: 3.4vw;
  }
  to {
    width: 1.5vw;
  }
}

.favoritesBarClosed > .favoriteToggle {
  width: 3.4vw;
  animation-name: resizeFavoritesToggler;
  animation-duration: 0.7s;
  animation-timing-function: ease-out;
}

@keyframes resizeFavoritesToggler {
  from {
    width: 1.5vw;
  }
  to {
    width: 3.4vw;
  }
}

.playTheme {
  background-color: #c9746e;
}

